import React from "react";
import styled from "styled-components";
import { Header, Separator, Text } from "@kyma-project/react-components";
import { sortByOrder, filterWithoutInternal } from "../../helpers/helpers";

const Wrapper = styled.div`
  box-sizing: border-box;
  width: 100%;
  margin: 0 0 20px 0;
  text-align: left;
  border-radius: 4px;
  background-color: #ffffff;
  box-shadow: 0 0 2px 0 rgba(0, 0, 0, 0.08);
  font-family: "72";
  font-weight: normal;
`;

const ContentHeader = styled.div`
  box-sizing: border-box;
  width: 100%;
  margin: 0;
  line-height: 1.19;
  padding: 16px 20px;
  &:first-letter {
    text-transform: uppercase;
  }
`;

const ContentDescription = styled.div`
  box-sizing: border-box;
  width: 100%;
  margin: 0;
  padding: 20px;
`;

const Anchor = styled.a`
  display: block;
  height: 0;
  overflow: none;
  visibility: hidden;
`;

function ContentWrapper(props) {
  let content = null,
    docs = null;
  if (!props.content.loading) {
    if (props.content.content) {
      content = props.content.content;
      if (content.docs) {
        docs = sortByOrder(content.docs);
        docs = filterWithoutInternal(docs);
      }
    }
  }

  let lastTypeHash;
  let removeSpaces = name => {
    return name
      .trim()
      .replace(/ /g, "-")
      .toLowerCase();
  };
  return (
    <div>
      {content && (
        <Wrapper>
          <ContentHeader>
            <Header>{content.displayName}</Header>
          </ContentHeader>
          <Separator />
          <ContentDescription>
            {docs &&
              docs.map((item, i) => {
                const hash = `${removeSpaces(item.type)}-${removeSpaces(
                  item.title,
                )}`;
                let isFirtsOfType = false;
                const currentTypeHash = `${removeSpaces(
                  item.type,
                )}-${removeSpaces(item.type)}`;

                isFirtsOfType = lastTypeHash !== currentTypeHash;
                lastTypeHash = currentTypeHash;

                return (
                  <div key={i}>
                    {isFirtsOfType && <Anchor id={currentTypeHash} />}
                    <Header id={hash}>{item.title}</Header>
                    <Text
                      dangerouslySetInnerHTML={{
                        __html: item.source,
                      }}
                    />
                  </div>
                );
              })}
            <Text>{props.description}</Text>
          </ContentDescription>
        </Wrapper>
      )}
    </div>
  );
}
export default ContentWrapper;
