import React from "react";
import MainPageContainer from "./external/components/MainPage/MainPage.container";
import { Switch, Route } from "react-router-dom";

const DocsContainer = ({ versions }) => {
  const version = versions[0];
  const pageName = "documentation"

  return (
    <Switch>
      <Route
        exact
        path={`/${pageName}/`}
        render={props => <MainPageContainer version={version} pageName={pageName} {...props} />}
      />
      <Route
        exact
        path={`/${pageName}/:type/:id`}
        render={props => <MainPageContainer version={version} pageName={pageName} {...props} />}
      />
    </Switch>
  );
};

export default DocsContainer;
