import React from "react";

import ContentWrapper from "./ContentWrapper.component";
import { getDocs } from "../../helpers/api";

export default class extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: true,
    };
  }
  async componentDidMount() {
    const type = this.props.item.type;
    const id = this.props.item.id;
    const version = this.props.version;

    const docs = await getDocs(version, type, id);

    this.setState({
      loading: false,
      content: { ...docs },
      ...docs,
    });
  }

  async componentWillReceiveProps(newProps) {
    const version = newProps.version;
    const type = newProps.item.type;
    const id = newProps.item.id;

    if (
      newProps.item.type !== this.props.item.type ||
      newProps.item.id !== this.props.item.id
    ) {
      this.setState({
        loading: true,
        ...this.state,
      });
      const docs = await getDocs(version, type, id);
      this.setState({
        loading: false,
        content: { ...docs },
        ...docs,
      });
    }
  }

  render() {
    return <ContentWrapper content={this.state} {...this.props} />;
  }
}
