import React from "react";
import MainPage from "./MainPage.component";
import { getNavigation, getManifest } from "../../helpers/api";

export default class extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      navigation: null,
      manifest: null,
    };
  }

  async componentDidMount() {
    const { version } = this.props;
    const navigation = await getNavigation(version);
    const manifest = await getManifest(version);

    this.setState({
      navigation,
      manifest,
    });
  }

  render() {
    if (!this.state.navigation || !this.state.manifest) {
      return null;
    }

    return (
      <MainPage
        topics={this.state.navigation}
        manifest={this.state.manifest.spec}
        {...this.props}
      />
    );
  }
}
