import React from "react";
import { Helmet } from "react-helmet";
import DocsContainer from "../components/docs/Docs.container";
import styled from "styled-components";
import DefaultLayout from "../components/layout/DefaultLayout";
import { graphql } from "gatsby";

const Wrapper = styled.section`
  max-width: 850px;
`;

const DocsPage = ({ data }) => {
  const edges = data.allDirectory.edges || [];
  const versions = edges.map(edge => edge.node.relativePath).sort();

  return (
    <DefaultLayout pageId="documentation">
      <Helmet />
      <Wrapper>
        <DocsContainer versions={versions} />
      </Wrapper>
    </DefaultLayout>
  );
};

export default DocsPage;

export const pageQuery = graphql`
  query DocsVersions {
    allDirectory(
      filter: {
        relativePath: { regex: "/^[^/]+$/" }
        sourceInstanceName: { eq: "docs" }
      }
    ) {
      edges {
        node {
          relativePath
        }
      }
    }
  }
`;
