import axios from "axios";
import {withPrefix} from "gatsby";

const DOCS_LOCATION = "docs";

export const getNavigation = async version => {
  const response = await axios(withPrefix(`/${DOCS_LOCATION}/${version}/navigation.json`));
  return response.data;
};

export const getDocs = async (version, type, id) => {
  const response = await axios(withPrefix(`/${DOCS_LOCATION}/${version}/${type}/${id}/content.json`));
  return response.data;
};

export const getManifest = async version => {
  const response = await axios(withPrefix(`/${DOCS_LOCATION}/${version}/manifest.json`));
  return response.data;
};
